'use client';

import { useState, useEffect } from 'react';
import { Download, DollarSign, CreditCard, Calendar, Receipt } from 'lucide-react';

interface Payment {
  _id: string;
  plan: string;
  amount: number;
  status: string;
  invoiceUrl?: string;
  createdAt: string;
}

interface PaginationData {
  page: number;
  limit: number;
  total: number;
  pages: number;
}

export default function PaymentsTab() {
  const [payments, setPayments] = useState<Payment[]>([]);
  const [pagination, setPagination] = useState<PaginationData | null>(null);
  const [loading, setLoading] = useState(true);
  const [page, setPage] = useState(1);
  const [userPaymentStats, setUserPaymentStats] = useState({
    totalSpent: 0,
    totalPayments: 0,
    currentPlan: 'Free',
    nextBilling: '',
    avgPayment: 0
  });

  useEffect(() => {
    fetchPayments();
  }, [page]);

  const fetchPayments = async () => {
    try {
      const response = await fetch(`/api/user/payments?page=${page}&limit=10`);
      if (response.ok) {
        const data = await response.json();
        const paymentsData = data.payments || [];

        setPayments(paymentsData);
        setPagination(data.pagination || { page: 1, limit: 10, total: paymentsData.length, pages: 1 });

        // Calculate user payment stats from the payments
        calculateUserPaymentStats(paymentsData);
      } else {
        console.error('Failed to fetch payments:', response.status);
        setPayments([]);
        setPagination(null);
        calculateUserPaymentStats([]);
      }
    } catch (error) {
      console.error('Error fetching payments:', error);
      setPayments([]);
      setPagination(null);
      calculateUserPaymentStats([]);
    } finally {
      setLoading(false);
    }
  };


  const calculateUserPaymentStats = (paymentsData: Payment[]) => {
    const totalSpent = paymentsData.reduce((sum, payment) => sum + payment.amount, 0);
    const totalPayments = paymentsData.length;
    const avgPayment = totalPayments > 0 ? Math.round(totalSpent / totalPayments) : 0;

    // Find current plan (most recent payment)
    const currentPlan = paymentsData.length > 0 ? paymentsData[0].plan : 'Free';

    // Calculate next billing date (assuming monthly subscription)
    const nextBilling = paymentsData.length > 0
      ? new Date(new Date(paymentsData[0].createdAt).getTime() + 30 * 24 * 60 * 60 * 1000).toISOString().split('T')[0]
      : '';

    setUserPaymentStats({
      totalSpent,
      totalPayments,
      currentPlan,
      nextBilling,
      avgPayment
    });
  };

  const downloadInvoice = (invoiceUrl: string) => {
    window.open(invoiceUrl, '_blank');
  };

  if (loading) {
    return <div className="text-white">Loading payments...</div>;
  }

  return (
    <div className="space-y-8">
      <div className="flex items-center gap-3">
        <CreditCard className="w-6 h-6 text-blue-500" />
        <h2 className="text-2xl font-bold text-white">My Payments</h2>
      </div>

      {/* Personal Payment Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="bg-slate-800 rounded-lg p-6 border border-slate-700">
          <div className="flex items-center gap-3">
            <div className="p-3 bg-green-500/20 rounded-lg">
              <DollarSign className="w-6 h-6 text-green-500" />
            </div>
            <div>
              <p className="text-slate-400 text-sm">Total Spent</p>
              <p className="text-2xl font-bold text-white">₹{userPaymentStats.totalSpent}</p>
            </div>
          </div>
        </div>

        <div className="bg-slate-800 rounded-lg p-6 border border-slate-700">
          <div className="flex items-center gap-3">
            <div className="p-3 bg-blue-500/20 rounded-lg">
              <Receipt className="w-6 h-6 text-blue-500" />
            </div>
            <div>
              <p className="text-slate-400 text-sm">Total Payments</p>
              <p className="text-2xl font-bold text-white">{userPaymentStats.totalPayments}</p>
            </div>
          </div>
        </div>

        <div className="bg-slate-800 rounded-lg p-6 border border-slate-700">
          <div className="flex items-center gap-3">
            <div className="p-3 bg-purple-500/20 rounded-lg">
              <CreditCard className="w-6 h-6 text-purple-500" />
            </div>
            <div>
              <p className="text-slate-400 text-sm">Current Plan</p>
              <p className="text-lg font-bold text-white">{userPaymentStats.currentPlan}</p>
            </div>
          </div>
        </div>

        <div className="bg-slate-800 rounded-lg p-6 border border-slate-700">
          <div className="flex items-center gap-3">
            <div className="p-3 bg-yellow-500/20 rounded-lg">
              <Calendar className="w-6 h-6 text-yellow-500" />
            </div>
            <div>
              <p className="text-slate-400 text-sm">Next Billing</p>
              <p className="text-lg font-bold text-white">
                {userPaymentStats.nextBilling ? new Date(userPaymentStats.nextBilling).toLocaleDateString() : 'N/A'}
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Payment History Table */}
      <div className="bg-slate-800 rounded-lg border border-slate-700 overflow-hidden">
        <div className="p-6 border-b border-slate-700">
          <h3 className="text-lg font-semibold text-white flex items-center gap-2">
            <CreditCard className="w-5 h-5 text-blue-500" />
            Payment History
          </h3>
        </div>

        {payments.length === 0 ? (
          <div className="p-8 text-center">
            <p className="text-slate-400">No payments yet</p>
          </div>
        ) : (
          <>
            <div className="overflow-x-auto">
              <table className="w-full text-left text-sm text-slate-300">
                <thead className="bg-slate-800 border-b border-slate-700">
                  <tr>
                    <th className="px-6 py-4 font-semibold">Plan</th>
                    <th className="px-6 py-4 font-semibold">Amount</th>
                    <th className="px-6 py-4 font-semibold">Status</th>
                    <th className="px-6 py-4 font-semibold">Date</th>
                    <th className="px-6 py-4 font-semibold">Invoice</th>
                  </tr>
                </thead>
                <tbody>
                  {payments.map((payment) => (
                    <tr key={payment._id} className="border-b border-slate-700 hover:bg-slate-800 transition">
                      <td className="px-6 py-4 font-semibold">{payment.plan}</td>
                      <td className="px-6 py-4">₹{payment.amount}</td>
                      <td className="px-6 py-4">
                        <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                          payment.status === 'completed'
                            ? 'bg-green-500/20 text-green-400'
                            : payment.status === 'pending'
                            ? 'bg-yellow-500/20 text-yellow-400'
                            : 'bg-red-500/20 text-red-400'
                        }`}>
                          {payment.status}
                        </span>
                      </td>
                      <td className="px-6 py-4">{new Date(payment.createdAt).toLocaleDateString()}</td>
                      <td className="px-6 py-4">
                        {payment.invoiceUrl ? (
                          <button
                            onClick={() => downloadInvoice(payment.invoiceUrl!)}
                            className="flex items-center gap-2 px-3 py-1 bg-blue-600 hover:bg-blue-700 text-white rounded-md transition text-sm"
                          >
                            <Download className="w-4 h-4" />
                            Download
                          </button>
                        ) : (
                          <span className="text-slate-500">N/A</span>
                        )}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>

            {/* Pagination */}
            {pagination && pagination.pages > 1 && (
              <div className="p-6 border-t border-slate-700">
                <div className="flex items-center justify-center gap-2">
                  <button
                    onClick={() => setPage(Math.max(1, page - 1))}
                    disabled={page === 1}
                    className="px-4 py-2 bg-slate-700 hover:bg-slate-600 disabled:bg-slate-800 disabled:text-slate-500 text-white rounded transition"
                  >
                    Previous
                  </button>

                  {Array.from({ length: pagination.pages }, (_, i) => i + 1).map((p) => (
                    <button
                      key={p}
                      onClick={() => setPage(p)}
                      className={`px-4 py-2 rounded transition ${
                        page === p
                          ? 'bg-blue-500 text-white'
                          : 'bg-slate-700 hover:bg-slate-600 text-white'
                      }`}
                    >
                      {p}
                    </button>
                  ))}

                  <button
                    onClick={() => setPage(Math.min(pagination.pages, page + 1))}
                    disabled={page === pagination.pages}
                    className="px-4 py-2 bg-slate-700 hover:bg-slate-600 disabled:bg-slate-800 disabled:text-slate-500 text-white rounded transition"
                  >
                    Next
                  </button>
                </div>
              </div>
            )}
          </>
        )}
      </div>
    </div>
  );
}
