'use client';

import { useState, useEffect } from 'react';
import { Check, X } from 'lucide-react';

interface UserProfile {
  name: string;
  email: string;
  role: string;
  isPremium: boolean;
  trialCount: number;
  subscriptionExpiry: string | null;
}

export default function ProfileTab() {
  const [user, setUser] = useState<UserProfile | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchUserProfile();
  }, []);

  const fetchUserProfile = async () => {
    try {
      const response = await fetch('/api/user/me');
      if (response.ok) {
        const data = await response.json();
        setUser(data.user);
      }
    } catch (error) {
      console.error('Error fetching profile:', error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return <div className="text-white">Loading...</div>;
  }

  if (!user) {
    return <div className="text-red-400">Failed to load profile</div>;
  }

  return (
    <div className="max-w-2xl">
      <h2 className="text-3xl font-bold text-white mb-8">Profile</h2>

      <div className="space-y-6">
        {/* User Information */}
        <div className="bg-slate-800 rounded-lg border border-slate-700 p-6">
          <h3 className="text-xl font-semibold text-white mb-4">Account Information</h3>
          <div className="space-y-4">
            <div>
              <p className="text-slate-400 text-sm">Name</p>
              <p className="text-white text-lg font-medium">{user.name}</p>
            </div>
            <div>
              <p className="text-slate-400 text-sm">Email</p>
              <p className="text-white text-lg font-medium">{user.email}</p>
            </div>
            <div>
              <p className="text-slate-400 text-sm">Account Type</p>
              <p className="text-white text-lg font-medium capitalize">{user.role}</p>
            </div>
          </div>
        </div>

        {/* Trial Information */}
        <div className="bg-slate-800 rounded-lg border border-slate-700 p-6">
          <h3 className="text-xl font-semibold text-white mb-4">Free Trials</h3>
          <div className="flex items-center gap-4">
            <div className="text-center">
              <p className="text-4xl font-bold text-blue-400">{user.trialCount}</p>
              <p className="text-slate-400">Trials Remaining</p>
            </div>
            <div className="flex-1">
              <div className="w-full bg-slate-700 rounded-full h-3">
                <div
                  className="bg-blue-500 h-3 rounded-full transition-all"
                  style={{ width: `${(user.trialCount / 5) * 100}%` }}
                ></div>
              </div>
              <p className="text-slate-400 text-sm mt-2">5 trials included for free</p>
            </div>
          </div>
        </div>

        {/* Subscription Status */}
        <div className={`rounded-lg border p-6 ${
          user.isPremium
            ? 'bg-green-900 border-green-700'
            : 'bg-yellow-900 border-yellow-700'
        }`}>
          <h3 className="text-xl font-semibold mb-4 flex items-center gap-2">
            {user.isPremium ? (
              <>
                <Check size={24} className="text-green-300" />
                <span className="text-green-300">Premium Active</span>
              </>
            ) : (
              <>
                <X size={24} className="text-yellow-300" />
                <span className="text-yellow-300">Free Plan</span>
              </>
            )}
          </h3>

          {user.isPremium ? (
            <div>
              <p className="text-green-200">You are enjoying full access to all tools!</p>
              {user.subscriptionExpiry && (
                <p className="text-green-200 text-sm mt-2">
                  Expires on: {new Date(user.subscriptionExpiry).toLocaleDateString()}
                </p>
              )}
            </div>
          ) : (
            <div className="space-y-4">
              <p className="text-yellow-200">Upgrade to premium for unlimited access</p>
              <a
                href="/pricing"
                className="inline-block bg-blue-500 hover:bg-blue-600 text-white px-6 py-2 rounded-lg transition"
              >
                View Pricing Plans
              </a>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
